.cards_popup .popup h1 {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #006aff;
    visibility: visible !important;
    cursor: pointer;
}

.cards_popup .popup h1:hover {
    opacity: 0.7;
    text-decoration: underline;
}

.cards_popup .popup .btn_popup {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 15px 0;
}

.cards_popup .popup {
    transition: display 0.1s ease-out !important;
}

.cards_popup .popup .btn_popup p {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #0cc14a;
}

.cards_popup .popup .btn_popup p span {
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    color: #0cc14a;
}

.cards_popup .popup .primary {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #999999;
}

.cards_popup .popup .product {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1c1c1c;
    margin-top: 10px;
}


