.cards_popup .popup .products {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #1c1c1c;
    margin-top: 5px;
    display: flex;
}

.cards_popup .popup .products::before {
    content: "";
    display: block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: black;
    margin-right: 10px;
    margin-top: 7px;
}

.cards_popup .popup .btn_popup button {
    border: none;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: white;
    background: red;
    border-radius: 5px;
    padding: 5px 15px;
}

.cards_popup .add_cart {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
}

.cards_popup .add_cart button {
    border: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #fcfcfc;
    background: #006aff;
    border-radius: 15px;
    padding: 12px 38px;
    cursor: pointer;
}

.scale0 {
    position: absolute;
    transform: scale(0.6);
    top: 0 !important;
    visibility: hidden;
}

.cards_popup .popup {
    transition: all 0.1s linear !important;
    transform: scale(1);
    visibility: visible;
}

.cards-left .visibilityNone {
    width: 370px;
    /* height: 478px; */
    max-width: 350px;
    border-radius: 15px;
    padding: 30px;
    position: absolute;
    top: 15px;
    left: -370px;
    z-index: -1;
}

.courseImage {
    width: 100%;
    aspect-ratio: 16/9 !important;
    border-radius: 15px;
    background-color: #f6f6f6;
    object-fit: fill;
}

