.course-item {
    width: 100%;
    border-radius: 15px 15px 0 0;
    margin-bottom: 50px;
    /* padding-top: 20px; */
    transition: all 0.2s linear;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 340px;
}

.course-item:last-child {
    margin-bottom: 0 !important;
}

.course-item .t-gray {
    color: #999999;
}

.course-item .line-through {
    text-decoration: line-through;
}

.course-item .item-header {
    width: 100%;
    border-radius: 15px;
    position: relative !important;
    /* margin-bottom: 20px; */
}

.course-item iframe {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 15px;
    cursor: pointer;
}

.course-item .lab {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px 25px;
    z-index: 1;
    border-radius: 5px 15px 5px 5px;
    font-weight: 600;
    font-size: 12px;
    line-height: 17px;
    color: #1c1c1c;
}

.course-item .label {
    position: absolute;
    bottom: 0px;
    right: 0;
    padding: 5px 15px;
    z-index: 1;
    border-radius: 5px 5px 15px 5px;
    font-family: "SF Pro Text", sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 17px;
    color: #1c1c1c;
}

.course-item .bg-green {
    background-color: #0cc14a;
}

.course-item .bg-orange {
    background-color: #f19d3b;
}

.course-item .bg-red {
    background-color: #f00;
    color: #fff;
}

.course-item .bg-goldenrod {
    background: blue;
    color: #fff;
    top: 0;
    right: 0;
    height: auto;
}

.course-item .item-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.course-item .item-row .title {
    text-align: left;
    font-family: "SF Pro Text", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #006aff;
    margin-right: 25px;
    cursor: pointer;
    /* margin-bottom: 0; */
    margin-top: 10px !important;
    transition: all 0.2s linear;
}

.course-item .item-row .title:hover,
.course-item .trainer:hover,
.course-item:hover .title {
    text-decoration: underline;
}

.course-item .item-row .bookmark {
    width: 25px;
    min-width: 25px;
    height: 25px;
    margin-top: 3px;
    cursor: pointer;
}

.course-item .item-row .bookmark:hover {
    -webkit-animation: shopping 1s ease-in-out infinite;
            animation: shopping 1s ease-in-out infinite;
}

.course-item .trainer {
    font-family: "SF Pro Text", sans-serif;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    color: #1c1c1c;
    /* margin-bottom: 12px; */
}

.course-item .reviews {
    display: flex;
    align-items: center;
    /* margin-bottom: 24px; */
    margin-top: 10px !important;
}

.course-item .reviews div {
    display: flex;
    align-items: center;
}

.course-item .reviews div svg {
    width: 20px;
    margin-right: 12px;
}

.course-item .reviews div p {
    margin-bottom: 0;
    font-family: "SF Pro Text", sans-serif;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    color: #006aff;
}

.course-item .reviews div p span {
    margin-left: 5px;
}

.course-item .reviews .left {
    margin-right: 25px;
}

.course-item .item-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.course-item .item-footer .price .t-gray {
    font-family: "SF Pro Text", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 19px;
    display: flex;
    align-items: center;
}

.course-item .item-footer .price .t-gray+p {
    font-family: "SF Pro Text", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    margin-bottom: 0;
}

.course-item .item-footer .mr-20 {
    margin-right: 25px;
}

.course-item .item-footer .footer-row {
    display: flex;
}

.mb10 {
    margin-bottom: 10px;
}

.course-item .item-footer .course-start {
    /* width: 60px; */
    /* height: 44px; */
    /* border: 1px solid #006aff; */
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.course-item .item-row .course-start {
     width: 60px;
     height: 44px; 
     border: 1px solid #006aff;
     border-radius: 15px;
     display: flex;
     align-items: center;
     justify-content: center;
     cursor: pointer;
     margin-top: 10px;
}
.course-item .item-footer .footer-row {
    display: flex;
}

.course-item .item-footer .course-start:hover svg {
    -webkit-animation: shopping 1s ease-in-out infinite;
            animation: shopping 1s ease-in-out infinite;
}

@-webkit-keyframes shopping {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes shopping {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


